export default "<template>\n  <div>\n    <div class=\"section\">\n      <h1>Notices</h1>\n      <p>This is used to point out new locations or features</p>\n    </div>\n    <div class=\"section\">\n      <h2>Tooltip Notice</h2>\n\n      <DemoSection title=\"Default\" :code=\"demoCode\">\n        <template #components>\n          <TooltipNotice\n            v-if=\"showTooltipNotice\"\n            title=\"Defaults\"\n            desc=\"All your favorite widgets, just with a new view.\"\n            @handle-tooltip=\"closeTooltip\"\n          />\n\n          <TooltipNotice\n            v-if=\"showTooltipNotice\"\n            title=\"Top arrow\"\n            desc=\"All your favorite widgets, just with a new view.\"\n            :arrowPosition=\"'top'\"\n            :hasButton=\"false\"\n            @handle-tooltip=\"closeTooltip\"\n          />\n\n          <TooltipNotice\n            v-if=\"showTooltipNotice\"\n            title=\"Custom button title\"\n            desc=\"All your favorite widgets, just with a new view.\"\n            :buttonTitle=\"'Get started'\"\n            @handle-tooltip=\"closeTooltip\"\n          />\n\n          <TooltipNotice\n            v-if=\"showTooltipNotice\"\n            title=\"Custom width\"\n            desc=\"All your favorite widgets, just with a new view.\"\n            :width=\"300\"\n            @handle-tooltip=\"closeTooltip\"\n          />\n\n          <TooltipNotice\n            v-if=\"showTooltipNotice\"\n            title=\"Secondary Action\"\n            desc=\"All your favorite widgets, just with a new view.\"\n            :hasSecondaryAction=\"true\"\n            secondaryActionTitle=\"No thanks\"\n            @handle-tooltip=\"alertTooltip\"\n            @handle-tooltip-secondary=\"closeTooltip\"\n          />\n        </template>\n      </DemoSection>\n\n      <table class=\"docs-table\">\n        <thead>\n          <tr>\n            <th>Props</th>\n            <th>Type</th>\n            <th>Default</th>\n            <th>Description</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr>\n            <td>arrowPosition</td>\n            <td>string</td>\n            <td>'left'</td>\n            <td>\n              Side to put the arrow on. Options are\n              <code>left</code> and\n              <code>top</code>\n            </td>\n          </tr>\n\n          <tr>\n            <td>buttonTitle</td>\n            <td>string</td>\n            <td>'Got It'</td>\n            <td>Button text</td>\n          </tr>\n\n          <tr>\n            <td>hasButton</td>\n            <td>boolean</td>\n            <td>true</td>\n            <td>Having a button is optional</td>\n          </tr>\n\n          <tr>\n            <td>hasSecondaryAction</td>\n            <td>boolean</td>\n            <td>false</td>\n            <td>Have a second action option</td>\n          </tr>\n\n          <tr>\n            <td>secondaryActionTitle</td>\n            <td>string</td>\n            <td>'Learn more'</td>\n            <td>Title for secondary action</td>\n          </tr>\n\n          <tr>\n            <td>width</td>\n            <td>number</td>\n            <td>200</td>\n            <td>Change the width of the tooltip</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport NoticesCode from \"./Notices.vue?raw\";\nimport TooltipNotice from \"./../components/TooltipNotice.vue\";\n\n@Component({\n  components: {\n    DemoSection,\n    TooltipNotice\n  }\n})\nexport default class Notices extends Vue {\n  demoCode = NoticesCode;\n  showTooltipNotice = true;\n\n  alertTooltip() {\n    window.alert(\"tooltip clicked\");\n  }\n\n  closeTooltip() {\n    this.showTooltipNotice = false;\n  }\n}\n</script>\n\n<style lang=\"less\" scoped>\n::v-deep .s-demo-section__content {\n  height: 330px;\n\n  .s-tooltip-notice {\n    &:nth-child(1) {\n      top: 0px;\n      left: 0px;\n    }\n\n    &:nth-child(2) {\n      top: 0;\n      left: 250px;\n    }\n\n    &:nth-child(3) {\n      top: 0;\n      left: 500px;\n    }\n\n    &:nth-child(4) {\n      top: 169px;\n      left: 0;\n    }\n\n    &:nth-child(5) {\n      top: 169px;\n      left: 350px;\n    }\n  }\n}\n</style>\n"